html {
  font-family: "Open-Sans", sans-serif;
  color: #111;
}

div {}

p {
  font-weight: 300;
  margin: 0;
}

hr {
  border-top: dotted 1px #666;
  margin: 0 5% 5px 5%;
}

a, a:visited {
  color: white;
}

h1, h2, h3, h4 {
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  margin: 0;
}

h1 {
  color: white;
}

h3 {
  font-size: 1em;
}

h4 {
  font-size: 14px;
}

body {
  margin: 0;
  background-color: white;
}

/* ID TAGS */

#flexcol {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

#flexrow {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#highlight {
  text-decoration: underline;
}

#icon {
  margin: 3px 10px;
  color: white;
}

#adminText {
  font-family: monospace;
  margin: 0;
  color: white;
  text-shadow: 1px 1px black;
  margin-right: auto;
}

button {
  background-color: #09f;
  border: 1px solid black;
  margin-left: 5px;
  color: white;
  min-width: 50px;
}

/* AUTH */

.auth {
  width: 100%;
  position: fixed;
  height: 40px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: white;
  font-family: monospace;
  border-bottom: solid 1px #444;
}

.authNeutral {
  background-color: #444;
}

.authAdmin {
  background-color: #09f;
}

.authNegative {
  background: -webkit-gradient(linear, right top, left top, from(#f33), to(#c33));
  background: -webkit-linear-gradient(right, #f33, #c33);
  background: -o-linear-gradient(right, #f33, #c33);
  background: linear-gradient(to left, #f33, #c33);
}

.authPositive {
  background: -webkit-gradient(linear, right top, left top, from(#3d3), to(#3b3));
  background: -webkit-linear-gradient(right, #3d3, #3b3);
  background: -o-linear-gradient(right, #3d3, #3b3);
  background: linear-gradient(to left, #3d3, #3b3);
}

/* HEADER */


.title {
  background: -webkit-gradient(linear, right top, left top, from(#09f), to(#06c));
  background: -webkit-linear-gradient(right, #09f, #06c);
  background: -o-linear-gradient(right, #09f, #06c);
  background: linear-gradient(to left, #09f, #06c);
  padding: 75px 15%;
  margin: 0 auto;
  -ms-flex-pack: center;
      justify-content: center;
}

.blbl {
  margin: auto 20px;
  font-size: 2.5em;
}

/* ADMIN PANEL */

.Admin {
  width: 570px;
  margin: 75px auto;
  background-color: white;
  padding: 15px 0;
  -webkit-box-shadow: 0px 3px 15px #333;
          box-shadow: 0px 3px 15px #333;
  border: solid 1px red;
}

.tooltip {
  color: #333;
  font-size: 12px;
  font-family: monospace;
  margin: auto;
}

.uiItem {
  padding: 0px 7% 15px 7%;
}

input {
  padding: 15px 5px;
  border: 1px solid #888;
  width: 100%
}

.adminContractName {
  width: 100%;
}

.adminDate {
  margin-left: 5px;
  width: 250px;
  min-width: 140px;
}

#masterBalance {
  margin: 0 30px;
}

/* PRESALE */

.Presale {
  background-color: white;
}

.presale-item {
  width: 570px;
  border: solid 1px #09f;
  margin: 40px auto;
  -webkit-box-shadow: 0px 3px 15px #333;
          box-shadow: 0px 3px 15px #333;
  padding: 15px 0;
}

.presale-heading {
  padding: 20px 7% 2px 7%;
}

.presale-content {
  padding: 15px 5%;
}

.presale-admin {
  margin: 0 5%;
  padding: 10px 3%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: end;
      justify-content: flex-end;
  background-color: red;
}

.address {
  font-family: monospace;
  font-size: 14px;
  width: 100%;
  text-align: center;
  padding: 20px 5%;
  background-color: lightyellow;
  resize: none;
  height: 20px;
}

.btnAdmin {
  margin-left: 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  background-color: black;
  -webkit-box-shadow: 1px 1px black;
          box-shadow: 1px 1px black;
  color: white;
  font-family: monospace;
  ;
}

/* ---- */

.token-checkmark {
  float: right;
  color: red;
}

.token-checkmark.checked {
  color: green;
}

/* FOOTER */

.Description {
  background-color: #e9e9e9;
  padding: 40px 30%;
  line-height: 1.5em;
}

.Description h2, .Description a {
  margin-bottom: 20px;
  line-height: 1.5em;
  color: black;
}

.Footer {
  background-color: #333;
  -ms-flex-align: center;
      align-items: center;
  font-size: 12px;
  padding: 20px 0
}

.Footer div {
  padding: 0 30%;
  margin-top: 5px;
}

.footerText {
  margin: 0 20px;
}

